// —Å—Ç–≤–æ—Ä—é—î–º–æ –∫–ª–∞—Å, —è–∫–∏–π –±—É–¥–µ –≤—ñ–¥–ø–æ–≤—ñ–¥–∞—Ç–∏ –∑–∞ —Ñ–µ—Ç—á –¥–∞–Ω–∏—Ö –∑ –±–µ–∫–µ–Ω–¥—É
// —Ü–µ —Ä–æ–±–∏—Ç—å—Å—è –¥–ª—è —Ç–æ–≥–æ, —â–æ–±–∏ —Ä–æ–∑–¥—ñ–ª–∏—Ç–∏ —Ñ–∫–Ω–∫—Ü—ñ–æ–Ω–∞–ª, —è–∫–∏–π —Ä–æ–±–∏—Ç—å —Ä—ñ–∑–Ω—ñ —Ä–µ—á—ñ

export default class NewsApiservice {
  // –≤ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä—ñ –≤–∫–∞–∑—É—î–º–æ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ, —è–∫—ñ –±—É–¥–µ –º–∞—Ç–∏ –µ–∫–∑–µ–º–ø–ª—è—Ä —Ü—å–æ–≥–æ –∫–ª–∞—Å—É —ñ –∫—É–¥–∏ –±—É–¥–µ–º–æ –∑–∞–ø–∏—Å—É–≤–∞—Ç–∏ –¥–∞–Ω—ñ –∑–∞–º—ñ—Å—Ç—å –≤–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è –≥–ª–æ–±–∞–ª—å–Ω–∏—Ö –∑–º—ñ–Ω–Ω–∏—Ö
  constructor() {
    this.searchQuery = '';
    this.page = 1;
  }

  fetchArticles() {
    //   –æ–±'—î–∫—Ç –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –ø–æ—à—É–∫—É, —è–∫–∏–π –ø–∞—Ä—Å–∏—Ç—å—Å—è –≤ –Æ–†–õ, —â–æ–±–∏ –Ω–µ –ø–∏—Å–∞—Ç–∏ –¥–æ–≤–∂–µ–ª–µ—Ö–Ω–∏–π –ª—ñ–Ω–∫
    const searchParams = new URLSearchParams({
      q: this.searchQuery,
      pageSize: 5,
      page: this.page,
    });

    //   –≤–ª–∞—Å–Ω–µ, –Æ–†–õ
    const url = `https://newsapi.org/v2/everything?${searchParams}`;

    //   –≤ —Ü–æ–º—É –æ–±'—î–∫—Ç—ñ –ø–µ—Ä–µ—Ä–∞—î–º–æ –∑–±–µ—Ä—ñ–≥–∞—î–º–æ –∑–∞–≥–æ–ª–æ–≤–∫–∏ (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥, –∫–ª—é—á –¥–æ—Å—Ç—É–ø—É –¥–æ –ê–ü–Ü)
    const options = {
      headers: {
        'X-Api-Key': 'b41603e50de9421aa47b2028dddacda6',
      },
    };

    // –º–∏ —Ö–æ—á–µ–º–æ, —â–æ–±–∏ —Ñ–µ—Ç—á –ø–æ–≤–µ—Ä–Ω—É–≤ –ø—Ä–æ–º—ñ—Å —ñ–∑ —Å—Ç–∞—Ç—Ç–µ—é, —è–∫–∏–π –º–∏ –¥–∞–ª—ñ –æ–ø—Ä–∞—Ü—é—î–º–æ —É –∑–æ–≤–Ω—ñ—à–Ω—å–æ–º—É –∫–æ–¥—ñ
    return fetch(url, options)
      .then(r => r.json())
      .then(i => {
        //   –ø—ñ—Å–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –û–ö —Ä–µ–∑—É–ª—å—Ç–∞—Ç—É —Ñ–µ—Ç—á—É, –∑–±—ñ–ª—å—à—É—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –Ω–∞ 1, –¥–ª—è —Ç–æ–≥–æ, —â–æ–± –Ω–∞—Å—Ç—É–ø–Ω–∏–π –∑–∞–ø–∏—Ç –π—à–æ–≤ –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω–∫—É 2
        this.incrementPage();

        return i.articles;
      });
  }
  // –º–µ—Ç–æ–¥ (—Ñ—É–Ω–∫—Ü—ñ—è –∑–±—ñ–ª—å—à–µ–Ω–Ω—è –∑–Ω–∞—á–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏)
  incrementPage() {
    this.page += 1;
  }
  //   –º–µ—Ç–æ–¥ (—Ñ—É–Ω–∫—Ü—ñ—è) –∑–±–∏–≤–∞–Ω–Ω—è –∑–Ω–∞—á–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –Ω–∞ 1 (–≤–∏–∫–ª–∏–∫–∞—î—Ç—å—Å—è –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ –∫–Ω–æ–ø–∫–∏ –ø–æ—à—É–∫—É, –∞ –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–æ—É–¥ –º–æ—Ä —Ü—å–æ–≥–æ —Å—Ü–µ–Ω–∞—Ä—ñ—é –Ω–µ –±—É–¥–µ, —Ç–æ–º—É —Å—Ç–æ—Ä—ñ–Ω–∫–∞ –∑–±—ñ–ª—å—à–∏—Ç—å—Å—è –Ω–∞ 1)
  resetPage() {
    this.page = 1;
  }

  // –≥–µ—Ç—Ç–µ—Ä–∏ —ñ —Å–µ—Ç—Ç–µ—Ä–∏
  get query() {
    return this.searchQuery;
  }

  set query(newQuery) {
    this.searchQuery = newQuery;
  }
}
