// —ñ–º–ø–æ—Ä—Ç—É—î–º–æ –∫–ª–∞—Å –¥–ª—è —Ñ–µ—Ç—á—É –¥–∞–Ω–∏—Ö
import NewsApiservice from './news-service';
// —ñ–º–ø–æ—Ä—Ç—É—î–º–æ –∫–ª–∞—Å —ñ–∑ –∫–Ω–æ–ø–∫–æ—é
// import loadMoreBtn from './load-more-btn';
import LoadMoreBtn from './load-more-btn';

const refs = {
  list: document.querySelector('.news-list'),
  form: document.querySelector('.js-search-form'),
};

const loadMoreBtn = new LoadMoreBtn({
  selector: '[data-action="load-more"]',
  hidden: true,
});

// —Ä–æ–±–∏–º–æ –µ–∫–∑–µ–º–ø–ª—è—Ä —Ü—å–æ–≥–æ –∫–ª–∞—Å—É (—Ç–µ–ø–µ—Ä –º–∏ –º–æ–∂–µ–º–æ –ø–µ—Ä–µ–¥–∞–≤–∞—Ç–∏ –≤ –Ω—å–æ–≥–æ –≤—Å—é –Ω–µ–æ–±—Ö—ñ–¥–Ω—É —ñ–Ω—Ñ—É –±–µ–∑ –ø–æ—Ç—Ä–µ–±–∏ –≤ —Å—Ç–≤–æ—Ä–µ–Ω–Ω—ñ –≥–ª–æ–±–∞–ª—å–Ω–∏—Ö –∑–º—ñ–Ω–Ω–∏—Ö)
const newsApiService = new NewsApiservice();

refs.form.addEventListener('submit', onSearch);
loadMoreBtn.refs.button.addEventListener('click', onLoadMore);

function onSearch(e) {
  e.preventDefault();
  // –ø–µ—Ä–µ–¥–∞—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è this.search.query –∑ —ñ–Ω–ø—É—Ç—É —â–æ–±–∏ newsApiService –∑–Ω–∞–≤, —â–æ —à—É–∫–∞—Ç–∏
  newsApiService.query = e.target.query.value;

  if (e.target.query.value === '') {
    return alert('Type smth');
  }

  // –≤–º–∏–∫–∞—î–º–æ –∫–Ω–æ–ø–∫—É
  loadMoreBtn.show();
  newsApiService.resetPage();
  clearAtriclesContainer();
  loadMoreBtn.disable();
  // fetchArticles() –ø–æ–≤–µ—Ä—Ç–∞—î –ø—Ä–æ–º—ñ—Å —ñ–∑ —Å—Ç–∞—Ç—Ç—è–º–∏ (–º–æ–∂–Ω–∞ –ø–æ–¥–∏–≤–∏—Ç–∏—Å—è —É –∫–ª–∞—Å—ñ (return fetch...))
  //  –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ, –Ω–∞ —Ü–µ–π –ø—Ä–æ–º—ñ—Å –º–∏ –º–æ–∂–µ–º–æ –≤—á–µ–ø–∏—Ç–∏ then —ñ –æ–±—Ä–æ–±–∏—Ç–∏ –π–æ–≥–æ —è–∫ –Ω–∞–º –ø–æ—Ç—Ä—ñ–±–Ω–æ
  newsApiService.fetchArticles().then(articles => {
    appendArticlesMarkup(articles);
    refs.list.classList.add('news-list-active');
    loadMoreBtn.enable();
  });
}

function onLoadMore() {
  loadMoreBtn.disable();
  newsApiService.fetchArticles().then(articles => {
    // clearAtriclesContainer();
    appendArticlesMarkup(articles);
  });
}

function appendArticlesMarkup(articles) {
  refs.list.insertAdjacentHTML('beforeend', createMarkup(articles));
}

function createMarkup(articles) {
  return articles
    .map(({ url, urlToImage, title, author, description }) => {
      return `<div class="element">
        <a href="${url}">
            <img src="${urlToImage}" alt=""/>
            <h2>${title}</h2>
            <p>Posted by: ${author}</p>
            <p>${description}</p>
        </a>
      </div>`;
    })
    .join('');
}

function clearAtriclesContainer() {
  refs.list.innerHTML = '';
}
